// src/components/Login.jsx
import { Helmet } from "react-helmet";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom"; 
import "./Login.css";
import { FaUser, FaLock, FaFacebookF, FaTwitter, FaGoogle } from "react-icons/fa";
import { signInWithPopup, signInWithEmailAndPassword, sendPasswordResetEmail } from "firebase/auth";
import { auth, googleProvider, facebookProvider, twitterProvider } from '../../Firebaseconfig';

const Login = () => {
  const [formData, setFormData] = useState({ username: "", password: "" });
  const [loginMessage, setLoginMessage] = useState("");
  const [loading, setLoading] = useState(false); 
  const navigate = useNavigate();

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      await signInWithEmailAndPassword(auth, formData.username, formData.password);
      setLoginMessage("ðŸŽ‰ Welcome back! Redirecting you to chat...");
      setFormData({ username: "", password: "" });
      setTimeout(() => navigate("/"), 1500);
    } catch (error) {
      setLoginMessage(error.message);
    } finally {
      setLoading(false);
    }
  };

  const handleSocialLogin = async (platform) => {
    setLoading(true);
    try {
      let provider;
      if (platform === "Google") provider = googleProvider;
      if (platform === "Facebook") provider = facebookProvider;
      if (platform === "Twitter") provider = twitterProvider;

      const result = await signInWithPopup(auth, provider);
      setLoginMessage(`ðŸŽ‰ Welcome ${result.user.displayName || "Chat Enthusiast"}! Redirecting to chat...`);
      setTimeout(() => navigate("/"), 1500);
    } catch (error) {
      setLoginMessage(error.message);
    } finally {
      setLoading(false);
    }
  };

const handleForgotPassword = async () => {
  if (!formData.username) {
    setLoginMessage("Please enter your email to reset password.");
    return;
  }

  try {
    await sendPasswordResetEmail(auth, formData.username);
    setLoginMessage(`âœ… Password reset email sent to ${formData.username}.`);
  } catch (error) {
    setLoginMessage(error.message);
  }
};


  return (
    <>
      <Helmet>
        <title>Login | ChatBot Companion</title>
        <meta name="description" content="Login to ChatBot Companion to chat with your AI assistant." />
      </Helmet>

      <div className="login-container">
        <form className="login-box" onSubmit={handleSubmit}>
          <h2>Welcome Back!</h2>
          <p className="login-subtext">Login to continue chatting with your AI buddy.</p>

          <div className="input-group">
            <FaUser className="input-icon" />
            <input
              type="email"
              name="username"
              placeholder="Enter your email"
              value={formData.username}
              onChange={handleChange}
              required
            />
          </div>

          <div className="input-group">
            <FaLock className="input-icon" />
            <input
              type="password"
              name="password"
              placeholder="Enter your password"
              value={formData.password}
              onChange={handleChange}
              required
            />
          </div>

          <div className="forgot-link">
            <span onClick={handleForgotPassword} style={{ cursor: "pointer", color: "#102d6dff" }}>
              Forgot password?
            </span>
          </div>

          <button type="submit" className="login-btn" disabled={loading}>
            {loading ? "Logging in..." : "LOGIN"}
          </button>

          <p className="signup-text">
            Don't have an account?{" "}
            <span onClick={() => navigate("/SignUp")} style={{ cursor: "pointer", color:"#090a0aff", fontWeight: "bold" }}>
              Sign Up
            </span>
          </p>

          <p className="signup-text">Or Login Using</p>

          <div className="social-login">
            <span onClick={() => handleSocialLogin("Facebook")} title="Login with Facebook"><FaFacebookF /></span>
            <span onClick={() => handleSocialLogin("Twitter")} title="Login with Twitter"><FaTwitter /></span>
            <span onClick={() => handleSocialLogin("Google")} title="Login with Google"><FaGoogle /></span>
          </div>
        </form>

        {loginMessage && (
          <div className="login-message">
            <p>{loginMessage}</p>
          </div>
        )}
      </div>
    </>
  );
};

export default Login;
